* {
	--color-first: #303E65;
	--color-second: #439BB7;
	--color-white: #fff;
	--color-warning: #EB5757;

	--color-1: #F6F9FF;
	--color-2: #F8FAFF; // input background
	--color-3: #7583AA; // input placeholder
	--color-4: #72C3DD; // button solid
	--color-5: #ECEFF7; // dashboard logo bg
	--color-6: #303E65; // dashboard back title

	--anim4: 0.4s ease-in-out;
	--anim2: 0.2s ease-in-out;
	--anim1: 0.1s ease;
}

$laptop: 1280px;
$tablet: 767px;
$mobile: 425px;

@mixin respond-to($media) {
	// > $mobile
	@if $media == mobi {
		@media screen and (max-width: $mobile) {
			@content;
		}

	// > $tablet
	} @else if $media == tablet {
		@media screen and (max-width: $tablet) {
			@content;
		}

	// > $laptop
	} @else if $media == laptop {
		@media screen and (max-width: $laptop) {
			@content;
		}
	}
}
