.home-page {
	position: relative;
	padding: 55px 0 18px;
	color: var(--color-white);

	&::before {
		content: "";
		position: fixed;
		left: 0;
		right: 50%;
		top: 0;
		bottom: 0;
		background: var(--color-first);

		@include respond-to(tablet) {
			right: 0;
			bottom: 50%;
		}
	}

	&::after {
		content: "";
		position: fixed;
		left: 50%;
		right: 0;
		top: 0;
		bottom: 0;
		background: var(--color-second);

		@include respond-to(tablet) {
			left: 0;
			top: 50%;
			bottom: 0;
		}
	}

	.container {
		position: relative;
		z-index: 5;
		display: flex;
		flex-direction: column;
		width: 100%;
		height: calc(100vh - 83px);
	}

	&__top {
		position: relative;
		display: flex;
		align-items: center;
		justify-content: space-between;

		&-logo {
			border: 1px solid #7583aa;
			padding: 5px;
			font-size: 0;
		}
	}

	&__middle {
		display: flex;
		flex: 1;
		align-items: center;
		justify-content: space-between;
		padding-top: 20px;
		gap: 40px;

		.col {
			width: 50%;

			&.left {
				.home-page__title-top {
					&::after {
						left: 0;
					}
				}

				@include respond-to(tablet) {
					background: var(--color-first);
				}
			}

			&.right {
				padding-left: 40px;
				text-align: right;

				.btn {
					margin-left: auto;

					@include respond-to(tablet) {
						margin-left: unset;
					}
				}

				.home-page__descr-title {
					text-align: left;
					color: var(--color-first);
				}

				.home-page__descr {
					text-align: left;
				}

				@include respond-to(laptop) {
					padding-left: unset;
				}

				@include respond-to(tablet) {
					padding-left: 40px;
					text-align: left;
					background: var(--color-second);
				}

				@include respond-to(mobi) {
					padding-left: 20px;
				}
			}

			@include respond-to(tablet) {
				position: relative;
				margin-right: -20px;
				margin-left: -20px;
				padding: 40px;
				width: calc(100% + 40px);
				text-align: left;
			}

			@include respond-to(mobi) {
				padding: 40px 20px;
			}
		}

		.btn {
			margin-bottom: min(4.5rem, 72px);
		}

		@include respond-to(tablet) {
			flex-direction: column;
			padding-top: unset;
			gap: unset;
		}
	}

	&__title {
		margin-bottom: 30px;
		font-weight: 700;
		font-size: 4rem;
		line-height: 4.875rem;

		&-top {
			position: relative;
			padding-bottom: 9px;
			font-weight: 700;
			font-size: 2rem;
			line-height: 2.4375rem;
			text-transform: uppercase;

			&::after {
				content: "";
				position: absolute;
				right: 0;
				bottom: 0;
				width: 100%;
				height: 1px;
				max-width: 310px;
				background: var(--color-white);

				@include respond-to(tablet) {
					left: 0;
					right: unset;
				}
			}
		}
	}

	&__descr {
		font-size: max(1rem, 14px);
		line-height: 19px;
		color: var(--color-1);

		&-wrapper {
			height: 170px;

			@include respond-to(tablet) {
				height: auto;
			}
		}

		&-title {
			margin-bottom: 24px;
			font-size: max(1.125rem, 16px);
			line-height: min(3.5rem, 18px);
			letter-spacing: 0.5em;
			text-transform: uppercase;
		}
	}

	&__bottom {
		width: 50%;
		text-align: center;

		&-links {
			display: flex;
			align-items: center;
			justify-content: center;
			margin-bottom: 13px;
		}

		a {
			display: flex;
			align-items: center;
			max-width: max-content;
			font-size: max(0.75rem, 11px);
			letter-spacing: 0.5em;
			text-transform: uppercase;

			&:not(:last-child) {
				margin-right: 14px;

				&::after {
					content: "";
					display: block;
					margin-left: 14px;
					width: 1px;
					height: 12px;
					background: var(--color-second);

					@include respond-to(tablet) {
						background: var(--color-first);
					}
				}
			}

			@include respond-to(tablet) {
				letter-spacing: 0.4em;
			}
		}

		.copyright {
			font-size: 11px;
			color: #7583aa;

			@include respond-to(tablet) {
				padding-bottom: 20px;
				color: #414960;
			}
		}

		@include respond-to(tablet) {
			width: 100%;
		}
	}

	&.sign-in {
		.home-page {
			&__middle {
				align-items: unset;
				padding-top: unset;

				&-wrapper {
					display: flex;
					align-items: center;
					height: 100%;
					min-height: 540px;
					@include respond-to(tablet){
						min-height: 750px;
					}
				}

				.right {
					text-align: left;
				}
			}

			&__title-top {
				padding-bottom: 15px;
				font-weight: 400;
				font-size: max(1.5rem, 20px);
				line-height: 29px;
			}
		}
	}

	@include respond-to(tablet) {
		padding: 20px 0;
		form.type-1 {
			margin-left: unset;
		}
	}
}
