// $name - имя шрифтового семейства;
// $file - имя файла;
// $weight - жирность шрифта (по-умолчанию установлено значение 400, но если мы передадим параметр, то значение по-умолчанию будет проигнорировано)
// $style - стиль начертания (тоже установлен по-умолчанию normal)

@mixin font-face($name, $file, $weight: 400, $style: normal) {
	@font-face {
		src:
			local("#{$file}"),
			url("../fonts/#{$file}.woff2") format("woff2"),
			url("../fonts/#{$file}.woff") format("woff");
		font-family: "#{$name}";
		font-weight: $weight;
		font-style: $style;
		font-display: swap;
	}
}

@include font-face("Montserrat", "Montserrat-Regular");
@include font-face("Montserrat", "Montserrat-Medium", 500);
@include font-face("Montserrat", "Montserrat-SemiBold", 600);
@include font-face("Montserrat", "Montserrat-Bold", 700);
@include font-face("WorkSans", "WorkSans-Medium", 500);
@include font-face("WorkSans", "WorkSans-SemiBold", 600);
@include font-face("WorkSans", "WorkSans-Light", 300);

@import "_doc_section/_mixins/_rightBlock";

.btn {
	display: block;
	cursor: pointer;

	&.btn--photo {
		padding: 14px 25px 14px 19px;
		width: 214px;
		font-size: 14px;

		&::before {
			content: "";
			display: block;
			margin-right: 8px;
			width: 15px;
			height: 15px;
			background: url("../img/icons/icon-upload-photo.svg") no-repeat;
			background-position: center;
			background-size: contain;
		}

		&::after {
			margin-left: auto;
		}
	}

	&--border {
		border: 2px solid var(--color-1);
		border-radius: 30px;
		padding: 14px;
		width: 100%;
		max-width: min(214px, 42vw);
		font-weight: 600;
		font-size: max(1rem, 12px);
		letter-spacing: 0.33em;
		text-align: center;
		text-transform: uppercase;
		color: var(--color-white);
	}

	&--solid {
		display: flex;
		justify-content: space-between;
		border: unset;
		border-radius: 5px;
		padding: 14px 20px;
		width: 100%;
		max-width: 225px;
		font-family: "WorkSans", sans-serif;
		font-size: 16px;
		line-height: 19px;
		color: var(--color-white);
		background: var(--color-4);

		&-second {
			background: var(--color-first);
		}
	}

	&--arrow {
		display: flex;
		align-items: center;

		&::after {
			content: "";
			display: block;
			width: 12px;
			height: 12px;
			background: url("../img/icons/icon-btn-arrow.svg") no-repeat;
			background-position: center;
			background-size: contain;
		}
	}

	&--link {
		color: var(--color-first);
	}
}

form {
	font-family: "WorkSans";

	textarea,
	input:not([type="file"], [type="checkbox"]) {
		border: unset;
		border-radius: 5px;
		padding: 16px 21px;
		width: 100%;
		font-weight: 500;
		font-size: 14px;
		line-height: 16px;
		color: var(--color-first);
		background: var(--color-2);

		&::placeholder {
			font-weight: 300;
			color: var(--color-3);
		}
	}

	textarea {
		width: calc(100% - 30px);
		resize: none;
	}

	.show-password {
		position: absolute;
		right: 25px;
		bottom: 14px;
		display: block;
		margin: unset;
		padding: unset;
		width: 18px;
		height: 18px;
		background: url("../img/icons/icon-hide-password.svg") no-repeat;
		background-size: contain;
		cursor: pointer;

		&:checked {
			opacity: 0.3;
		}
	}

	label {
		margin-bottom: 6px;
		padding-left: 15px;
		font-weight: 600;
		font-size: 13px;
		color: var(--color-first);
	}

	.btn--link {
		margin-top: 35px;
		padding-left: 15px;
		min-width: max-content;
		font-weight: 600;
		font-size: 13px;
	}

	.form {
		&_input_wrap {
			display: flex;
			flex-direction: column-reverse;

			&.password {
				position: relative;
			}
		}
	}

	&.type-1 {
		display: flex;
		flex-direction: column;
		margin-left: auto;
		max-width: 417px;
		gap: 25px;

		.btn {
			margin-top: 75px;
		}
	}
}

.nav {
	display: flex;
	align-items: stretch;
	background: var(--color-2);

	&__logo {
		padding: 23px 26px;
		font-size: 0;
		background: var(--color-5);

		img {
			width: 29px;
			height: 36px;
		}
	}

	&__title {
		position: relative;
		display: flex;
		align-items: center;
		padding: 10px 14px 10px 30px;
		min-height: 100%;
		max-width: min-content;
		font-weight: 700;
		font-size: 18px;
		line-height: 18px;
		color: var(--color-3);

		&::after {
			content: "";
			position: absolute;
			right: 0;
			top: 0;
			bottom: 0;
			width: 1px;
			height: 100%;
			background: #e5eaf5;
		}
	}

	&__back {
		display: flex;
		flex-direction: column;
		justify-content: center;
		padding-left: 35px;
		font-family: "WorkSans", sans-serif;

		a {
			display: flex;
			align-items: center;
			margin-bottom: 5px;
			font-size: 12px;
			line-height: 14px;

			&::before {
				content: "";
				display: block;
				margin-right: 5px;
				margin-left: 2px;
				width: 12px;
				height: 12px;
				background: url("../img/icons/icon-arrow-back.svg") no-repeat;
				background-position: center;
				background-size: contain;
			}
		}

		span {
			font-weight: 300;
			font-size: 24px;
			line-height: 28px;
			color: var(--color-6);
		}
	}

	&__right {
		display: flex;
		align-items: center;
		margin-right: 65px;
		margin-left: auto;

		.btn {
			width: 214px;
			font-size: 14px;
		}
	}
}

span {
	.select2-selection__arrow {
		top: 50% !important;
		height: 10px !important;
		transform: translateY(-50%);

		b {
			left: unset !important;
			top: unset !important;
			margin-top: unset !important;
			margin-left: unset !important;
			border: unset !important;
			width: 10px !important;
			height: 10px !important;
			background: url("../img/icons/icon-arrow-dropdown.svg") no-repeat;
			background-position: center;
			background-size: contain;
			transition: var(--anim2);
		}
	}

	&.select2-container {
		// width: 100% !important;
		.select2-selection--single {
			padding: 16px 21px;
			height: unset;
		}

		&--open .select2-selection__arrow b {
			transform: rotate(180deg);
		}

		&--default {
			.select2-results__option--highlighted.select2-results__option--selectable {
				background-color: var(--color-second) !important;
			}

			.select2-selection--single {
				border: unset;
				border-radius: 5px;
				font-weight: 300;
				background-color: var(--color-2);

				.select2-selection__rendered {
					padding: unset;
					line-height: 1;
					color: var(--color-6);
				}
			}
		}
	}

	&.select2-container--below {
		.select2-selection__rendered {
			font-weight: 500 !important;
		}
	}

	&.select2-dropdown {
		border: unset;
		color: var(--color-6);
		background-color: var(--color-2);

		.select2-results__option {
			padding: 11px 20px;

			&:nth-child(1) {
				display: none;
			}
		}
	}
}

.form-profile {
	margin: 0 auto;
	padding: 30px 0;
	max-width: 1350px;

	.row {
		display: flex;

		&-field {
			&:not(:last-child) {
				margin-bottom: 24px;
			}
		}

		&.base {
			margin-bottom: 65px;
		}

		&.contact {
			margin-bottom: 55px;
		}

		&.picture {
			margin-bottom: 60px;
		}

		&.about,
		&.company {
			margin-bottom: 30px;
		}

		&.membership {
			margin-top: 77px;
		}

		&.notifications {
			margin-top: 90px;

			.form_input_wrap {
				&:not(:last-child) {
					margin-bottom: 16px;
				}
			}

			input {
				&:checked {
					+ label {
						position: relative;

						&::before {
							background: var(--color-first);
						}

						&::after {
							content: "";
							position: absolute;
							left: 3px;
							display: block;
							width: 10px;
							height: 10px;
							background: url("../img/icons/icon-checked.svg") no-repeat;
							background-position: center;
							background-size: contain;
						}
					}
				}

				+ label {
					display: flex;
					align-items: center;
					padding-left: unset;
					cursor: pointer;

					&::before {
						content: "";
						display: block;
						margin-right: 15px;
						border-radius: 4px;
						width: 16px;
						height: 16px;
						box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.05), inset 0 5px 20px rgba(0, 0, 0, 0.03);
						background: #f8faff;
					}
				}
			}
		}
	}

	.col {
		&.fields {
			width: 100%;

			.form_input_wrap {
				width: calc(50% - 30px);

				&:not(:last-child) {
					margin-right: 30px;
				}
			}

			&.textarea {
				.form_input_wrap {
					width: 100%;
				}
			}
		}

		&.title {
			padding-left: 7.4%;
			width: 49%;
			font-weight: 300;
			font-size: 24px;
			line-height: 1;
			color: var(--color-6);

			span {
				font-weight: 500;
				color: var(--color-warning);
			}
		}
	}

	.picture {
		.form_input_wrap {
			flex-direction: row;
			align-items: center;
			width: max-content;

			input {
				display: none;
			}
		}
	}

	.add-photo {
		overflow: hidden;
		margin-right: 25%;
		border-radius: 100%;
		padding: 25px;
		width: 84px;
		height: 84px;
		min-width: 84px;
		background: var(--color-4);

		span {
			display: block;
			width: 100%;
			height: 100%;
			background: url("../img/icons/icon-add-photo.svg") no-repeat;
			background-size: contain;
		}

		&.show {
			padding: unset;

			span {
				display: none;
			}

			#show-photo {
				display: flex;
				justify-content: center;
				width: 84px;
			}

			img {
				height: 84px;
				object-fit: cover;
			}
		}
	}

	.btn--solid-second {
		padding: 16px 21px;
		min-width: max-content;
		font-size: 14px;
	}

	.send {
		margin-top: 77px;
		margin-right: 30px;
		margin-left: auto;
		min-width: max-content;
	}
}

.dashboard-wrapper {
	display: flex;

	.profile__wrapper {
		width: 100%;
	}

	.sidebar {
		display: flex;
		flex-direction: column;
		padding: 25px 28px;
		background: var(--color-first);
		gap: 25px;

		img {
			width: 25px;
			height: 22px;
		}
	}
}
